function Accordion({idToShow, children}) {
	function toogleHelp() {
		var x = document.getElementById(idToShow);
		if (x.className.indexOf("w3-show") == -1)
			x.className += " w3-show";
		else
			x.className = x.className.replace(" w3-show", "");
	}
	return (
		<button onClick={toogleHelp} className="w3-button w3-block w3-blue w3-left-align w3-round">{children}</button>
	);
}

export default function Help() {
	return (
		<>
			<div className="w3-card-4 w3-round w3-content formBackground" style={{maWwidth: 850}}>
				<div className="w3-container w3-blue w3-round">
					<h1>Instrucciones para conseguir los ficheros CSV</h1>
				</div>
				<h2 className="w3-margin-left">e-distribución o Enphase</h2>
				<div className="w3-margin-left w3-w3-panel w3-large">
					<p>
						Actualmente la web soporta dos tipos de fichero CSV con datos de consumo y producción de energía: e-distribución y Enphase.
					</p>
					<p>
						Para simular la factua de la luz con mayor precisión es recomendable usar el CSV de e-distribución ya
						que usa los datos reales que la distribuidora envía a la comercializadora de electricidad para generar
						la factura.
					</p>
					<p>
						Sin embargo, en caso de tener un contrato de autoconsumo y tu instalación fotovoltaica es de Enphase, puede ser interesante 
						usar el CSV de Enphase para	simular el importe de la factura en el mercado regulado PVPC si no tuvieramos placas fotovoltaicas.
					</p>
				</div>
				<div className="w3-padding"></div>
			</div>
			<div className="w3-panel">
				<p></p>
			</div>
			<div className="w3-card-4 w3-round w3-content formBackground" style={{maWwidth: 850}}>
				<Accordion idToShow='edistribucion'><h2>Conseguir CSV de e-distribución</h2></Accordion>
				<div className="w3-margin-left w3-w3-panel w3-large w3-hide" id="edistribucion">
					<p>
						1. Dirígete a la web de <a href="https://zonaprivada.edistribucion.com/areaprivada/s/wp-register?language=es" target="_blank">e-distribución</a> y registrate.
					</p>
					<p>
						2. Una vez entres en tu cuenta de e-distribución selecciona el recuadro de "TU CONSUMO".
					</p>
					<p>
						3. Selecciona el contrato del que quieres simular la factura y pulsa en "Consultar Curva".
					</p>
					<p>
						4. Selecciona el periodo de tiempo que quieres simular y pulsa "CONSULTAR".
					</p>
					<p>
						5. Cuando veas la curva de consumos, bajo la gráfica, selecciona la opción "Todas las magnitudes de energía" y pulsa sobre
						el botón "Descargar .CSV consumos"
					</p>
					<p>
						<a href="https://telegra.ph/Descarga-fichero-csv-de-e-distribuci%C3%B3n-12-28" target="_blank">Aquí</a> tienes una web donde explica todo el proceso de forma muy visual.
					</p>
					<div className="w3-padding"></div>
				</div>
			</div>
			<div className="w3-panel">
				<p></p>
			</div>
			<div className="w3-card-4 w3-round w3-content formBackground" style={{maWwidth: 850}}>
				<Accordion idToShow='Enphase'><h2>Conseguir CSV de Enphase</h2></Accordion>
				<div className="w3-margin-left w3-w3-panel w3-large w3-hide" id="Enphase">
					<p>
						1. Accede a tu cuenta de <a href="https://enlighten.enphaseenergy.com">Enphase</a>.
					</p>
					<p>
						2. Entra en el menú desplegable superior derecho -{'>'} Sistema -{'>'} Informes.
					</p>
					<p>
						3. Pulsa en el desplegable "Generación Diaria" -{'>'} "Energía Neta Mensual".
					</p>
					<p>
						3. Haz click en el icono del sobre que hay a la derecha. Ésto te mandará un correo electrónico a tu cuenta con el fichero CSV.
					</p>
					<p>
						<a href="https://www4.enphase.com/es-es/support/%C2%BFc%C3%B3mo-genero-un-informe" target="_blank">Aquí</a> puedes encontrar la documentación oficial de Enphase.
					</p>
					<div className="w3-padding"></div>
				</div>
			</div>
		</>
	);
}